/**
 * mobile navbar
 *
 * @package postnl_e-learning
 * @link http://postnl.nl/e-learning
 * @author 636
 * @since 1.0.0
 */
.pnl-mobile {
  &__search {
    .pnl-card__body {
      padding: .25rem 1.125rem;

      .form-control {
        border: none;
        padding: 0;
        font-weight: $font-weight-light;
        color: $gray-dark;
      }

      .form-control:focus {
        border: none;
        box-shadow: none;
      }
    }
  }

  &__navbar {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    height: 3.125rem;
    width: 100vw;
    background-color: $white;
    @extend .pnl-shadow;

    @include media-breakpoint-up(sm) {
      display: none;
      visibility: hidden;
    }
  }
}