//
// Make background tint
@mixin make-bg-color($key, $tint) {
  .bg--#{$key} {
    background-color: $tint;
  }
}
//
@each $key, $tint in $brand-colors {
  @include make-bg-color($key, $tint);
}


//
// Make background gradient
@mixin make-bg-gradient($key, $tint) {
  .gradient-#{$key} {
    background-image: linear-gradient(130deg, map-get($tint, start), map-get($tint, end));
  }
}
//
@each $key, $tint in $brand-gradients {
  @include make-bg-gradient($key, $tint);
}


//
// Make text tint
@mixin make-text-color($key, $tint) {
  .text--#{$key} {
    color: $tint;
  }
}
//
@each $key, $tint in $brand-colors {
  @include make-text-color($key, $tint);
}


//
// Make illustrations
@mixin make-illustration($key, $src) {
  .illustration-#{$key} {
    background: url($src)center center/80% no-repeat;
    position: absolute;
    right: 1.25rem;
    top: -.75rem;
    height: 7.375rem;
    width: 7.375rem;
    display: block;
  }

  .pnl-grid-featured {
    .illustration-#{$key} {
      right: -1.75rem;
      top: 50%;
      transform: translateY(-50%);
      height: 12.375rem;
      width: 12.375rem;
      opacity: .5;

      @include media-breakpoint-up(lg) {
        height: 22.375rem;
        width: 22.375rem;
        right: -4.75rem;
      }
    }
  }
}
//
@each $key, $src in $theme-illustrations {
  @include make-illustration($key, $src);
}