//
// Color system
//

// stylelint-disable
$white:    #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black:    #000 !default;

$grays: () !default;
$grays: map-merge((
  "100": $gray-100,
  "200": $gray-200,
  "300": $gray-300,
  "400": $gray-400,
  "500": $gray-500,
  "600": $gray-600,
  "700": $gray-700,
  "800": $gray-800,
  "900": $gray-900
), $grays);

$blue:    #3440B6 !default;
$indigo:  #6610f2 !default;
$purple:  #6f42c1 !default;
$pink:    #e83e8c !default;
$red:     #dc3545 !default;
$orange:  #ed8c00 !default;
$yellow:  #ffc107 !default;
$green:   #28a745 !default;
$teal:    #20c997 !default;
$cyan:    #17a2b8 !default;

$colors: () !default;
$colors: map-merge((
  "blue":       $blue,
  "indigo":     $indigo,
  "purple":     $purple,
  "pink":       $pink,
  "red":        $red,
  "orange":     $orange,
  "yellow":     $yellow,
  "green":      $green,
  "teal":       $teal,
  "cyan":       $cyan,
  "white":      $white,
  "gray":       $gray-600
), $colors);

//
// PostNL Brand colors
$orange-intense:        #e75204 !default;
$orange:                #ed8c00 !default; // primary
$orange-light:          #ffad00 !default;
$red:                   #d70036 !default; // advertisement
$blue-deep:             #001a73 !default;
$blue:                  #3440B6 !default; // primary
$blue-light:            #00a1e1 !default;
$gray-dark:             #27324c !default; // body-text
$gray-middle:           #66728a !default;
$gray:                  #adb5c5 !default; // pay-off
$gray-light:            #d4d9e3 !default; // primary
$gray-toon:             #f3f4f7 !default;

$primary:       $orange     !default;
$secondary:     $blue       !default;
$success:       $green      !default;
$info:          $cyan       !default;
$warning:       $yellow     !default;
$danger:        $red        !default;
$light:         $gray-100   !default;
$dark:          $gray-800   !default;


$theme-colors: () !default;
$theme-colors: map-merge((
  "primary":            $primary,
  "secondary":          $secondary,
  "success":            $success,
  "info":               $info,
  "warning":            $warning,
  "danger":             $danger,
  "light":              $light,
  "dark":               $dark,
), $theme-colors);


$brand-colors: () !default;
$brand-colors: map_merge((
  "orange-intense":     $orange-intense,
  "orange":             $orange,
  "orange-light":       $orange-light,
  "red":                $red,
  "blue-deep":          $blue-deep,
  "blue":               $blue,
  "blue-light":         $blue-light,
  "gray-dark":          $gray-dark,
  "gray-middle":        $gray-middle,
  "gray":               $gray,
  "gray-light":         $gray-light,
  "gray-toon":          $gray-toon
), $brand-colors);

$brand-gradients: () !default;
$brand-gradients: map_merge((
  "orange": (
    "start":            $orange-light,
    "end":              $orange-intense,
  ),
  "blue": (
    "start":            $blue,
    "end":              $blue-deep,
  ),
), $brand-gradients);

$theme-illustrations: () !default;
$theme-illustrations: map_merge((
  "identity":           "../img/identity.svg",
  "connected-world":    "../img/connected_world.svg",
  "deliveries":         "../img/deliveries.svg",
  "empty":              "../img/empty.svg",
  "envelope":           "../img/envelope.svg",
  "fishing":            "../img/fishing.svg",
  "location":           "../img/location.svg",
  "logistics":          "../img/logistics.svg",
  "stacking-packages":  "../img/stacking_packages.svg",
  "the-force":          "../img/the_force.svg",
), $theme-illustrations);


// stylelint-enable

// Set a specific jump point for requesting color jumps
// $theme-color-interval:      8% !default;

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
// $yiq-contrasted-threshold: 150 !default;

// Customize the light and dark text colors for use in our YIQ color contrast function.
// $yiq-text-dark: $gray-900 !default;
// $yiq-text-light: $white !default;

//
// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-caret:              false   !default;
// $enable-rounded:            false   !default;
// $enable-shadows:            true    !default;
//$enable-gradients:          true    !default;
$enable-transitions:        true    !default;
// $enable-grid-classes:       true    !default;
// $enable-print-styles:       true    !default;


//
// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

// stylelint-disable
$spacer: 1rem !default;
$spacers: () !default;
$spacers: map-merge((
  0: 0,
  1: ($spacer * .25),
  2: ($spacer * .5),
  3: $spacer,
  4: ($spacer * 1.5),
  5: ($spacer * 3)
), $spacers);

// This variable affects the `.h-*` and `.w-*` classes.
$sizes: () !default;
$sizes: map-merge((
  25: 25%,
  50: 50%,
  75: 75%,
  100: 100%
), $sizes);
// stylelint-enable


// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1360px
) !default;

// Grid columns
//
// Set the number of columns and specify the width of the gutters.

// $grid-columns:               12 !default;
$grid-gutter-width:      1.25rem !default;


//
// Body
//
// Settings for the `<body>` element.
$body-bg:                   $gray-100 !default;
$body-color:                $gray-900 !default;


// Links
//
// Style anchor elements.
$link-color:                theme-color("primary") !default;
$link-decoration:           none !default;
$link-hover-color:          lighten(theme-color("primary"), 10%) !default;
// $link-hover-decoration:     underline !default;


//
// Type
//
// Type, fonts, line-height, and color for body text, headings, and more.
//
// @since 1.0.0
//
$font-family-primary:       "PostNL Sans-serif", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
//$fonts-family-secondary:     "Josefin Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;

$font-family-sans-serif:    $font-family-primary;
$font-family-base:          $font-family-sans-serif !default;

$font-weight-light:           300 !default; // Default 300
$font-weight-normal:          400 !default; // Default normal
$font-weight-medium:          500 !default;
$font-weight-bold:            700 !default; // Default bold

$font-size-base:              1rem !default;
$font-size-lg:                1.25rem !default;
$font-size-sm:                .7rem !default;

$font-weight-base:            $font-weight-normal !default;
$line-height-base:            1.5 !default;

$headings-color:              theme-color("primary") !default;
// $headings-color:            theme-color("secondary") !default;
$headings-font-family:        $font-family-primary !default;
$headings-font-weight:        700 !default;
//$headings-line-height:        1.2 !default;


// Paragraphs
//
// Style p element.
$paragraph-margin-bottom:   1rem !default;


// Navbar
//
// ...
$fr-navbar-height:                  3rem !default;

$navbar-dark-color:                 rgba($white, .5) !default;
$navbar-dark-hover-color:           rgba($white, .75) !default;
$navbar-dark-active-color:          $white !default;
$navbar-dark-disabled-color:        rgba($white, .25) !default;

$navbar-light-color:                rgba($black, .8) !default;
$navbar-light-hover-color:          rgba($black, .6) !default;
$navbar-light-active-color:         rgba($black, .8) !default;
$navbar-light-disabled-color:       rgba($black, .3) !default;
