/**
 * navbar
 *
 * @package postnl_e-learning
 * @link http://postnl.nl/e-learning
 * @author 636
 * @since 1.0.0
 */
.pnl-navbar {
  .nav-item {
    .nav-link {
      font-weight: $font-weight-light;
      color: $gray-dark;

      &:hover, &:focus, &:active {
        color: rgba($gray-dark, .8);
      }

      &:focus, &:active {
        color: $gray-dark;
      }
    }

    &.active {
      .nav-link {
        font-weight: $font-weight-medium;
        color: $orange;

        &:hover {
          color: rgba($orange, .8);
        }

        &:focus, &:active {
          color: $orange;
        }
      }
    }
  }
}