/**
 * cards
 *
 * @package postnl_e-learning
 * @link http://postnl.nl/e-learning
 * @author 636
 * @since 1.0.0
 */
.pnl {

  &-grid {
    display: flex;
    flex-direction: column;
    flex-flow: row wrap;
    margin-bottom: $grid-gutter-width;

    >.pnl-card,
    .card {
      flex: 1 0 0%;
      margin-bottom: 0;

      +.pnl-card,
      .card {
        margin-left: 0;
        border-left: 0;
      }
    }

    .pnl-card {
      overflow: hidden;
      word-break: break-word;
      hyphens: auto;
      background-color: $white;
      color: $gray-dark;
      position: relative;
      display: flex;
      flex-direction: column;
      min-width: 0;
      background-clip: border-box;
      border: none;
      border-radius: .9375rem .9375rem 0 .9375rem;
      margin: 0 $grid-gutter-width;
      @extend .pnl-shadow, .card;

      &__body {
        display: -webkit-box;
        display: flex;
        z-index: 1;
        flex-direction: column;
        flex: 1 1 auto;
        padding: 1.25rem;
        text-decoration: none !important;

        &-label {
          font-size: .575rem;
          font-weight: $font-weight-medium;

          &--pill {
            background-color: $white;
            padding: ($spacer * .2) ($spacer * .5);
            border-radius: 2rem;
          }
        }

        &-title {
          flex-grow: 1;
          display: -webkit-box;
          display: flex;
          justify-content: flex-end;
          flex-direction: column;
          color: $gray-dark;
          margin-bottom: 0;
          font-size: 1.125rem;
          font-weight: $font-weight-medium;
        }

        &-subtitle {
          color: $gray;
          margin-bottom: .625rem;
          font-size: .875rem;
          font-weight: $font-weight-light;
        }

        &-progress {
          height: .125rem;
          background-color: rgba($gray-light, .6);
        }

      }
    }

    &-featured {
      height: 8.5rem;

      @include media-breakpoint-up(lg) {
        height: 16rem;
      }

      .pnl-card {
        &__body {
          &-title,
          &-subtitle {
            color: $white;
          }

          &-progress-bar {
            background-color: $white;
          }
        }
      }
    }

  }
}